.ProductListItem{
  
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 4em;
  margin: 5% 20% 0% 20%;
  overflow: overlay;
}
.ProductListItem::-webkit-scrollbar{
  width: 0;
  }


  @media screen and (max-width:900px) {
    .ProductListItem{

      grid-template-columns: 1fr 1fr ;
    }
  }


  
  @media screen and (max-width:768px) {
    .ProductListItem{
      margin: 5% 1em 0% 1em;
    }
  }